import React, { useState } from "react";
import styles from './Paginator.module.css';
import cn from "classnames"


let Paginator = ({currentPage, totalItemsCount, pageSize, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil (totalItemsCount / pageSize);

    let pages = [];
    for (let i=1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionNumber = portionNumber * portionSize;

    return <div className={cn (styles.paginator, styles.divWrapper)}>
        { portionNumber > 1 && <button onClick={() => {setPortionNumber(portionNumber - 1) }}>Prev</button>}

            {pages
            .filter(p => p >= leftPortionPageNumber && p<= rightPortionNumber)
            .map(p => {
                return <span className={ cn({[styles.selectedPage]:currentPage === p}, styles.pageNumber)} key={p} onClick={(e) => {onPageChanged(p)}}>{p}</span>
            })}
        { portionCount > portionNumber && <button onClick={() => {setPortionNumber(portionNumber + 1) }}>Next</button>}
        </div>
}

export default Paginator;